import * as React from "react"
import PropTypes from "prop-types"
import "../pages/bulma.scss"

const Footer = () => (
    <footer className='footer'>
        <div className='content has-text-centered'>
            <small className='has-text-white'>Copyright  2020 Community Improvement Service. All Rights Reserved.</small>
        </div>
    </footer>
)

Footer.propTypes = {
  siteTitle: PropTypes.string,
}

Footer.defaultProps = {
  siteTitle: ``,
}

export default Footer
