import * as React from "react"
import { useInView } from "react-intersection-observer"
import PropTypes from "prop-types"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image";
import './header.css'

const Header = ({ page }) => {
    const [isActive, setIsActive] = React.useState(false);
    const [ref, inView] = useInView({
        threshold: 0,
        initialInView: true
    })
    return (
        <header style={{fontFamily: `sans-serif`}}>
            <div className="has-text-centered"
                 style={{background: `rgb(201 218 248)`, paddingTop: `1em`, paddingBottom: `1em`, marginBottom: `${inView ? "" : "58.5px"}`}} ref={ref}>
                <div style={{display: "inline-block"}}>
                    <p className="announcementText">We urgently need funding to support our work. Thank you!</p>
                </div>
                <div style={{paddingLeft: 23, display: "inline-block"}}>
                    <Link to='/get-involved/donate' className="button is-inline is-outlined is-black">
                        <span style={{
                            paddingLeft: 5,
                            paddingRight: 5,
                            fontFamily: `sans-serif`,
                            fontSize: 16
                        }}>Donate</span>
                    </Link>
                </div>
            </div>
            <nav className={`navbar is-black ${inView ? "" : "navStick"}`} role="navigation" aria-label="main navigation">
                <div className="navbar-brand has-background-black-bis" style={{paddingLeft: 20}}>
                    <Link to='/' className="navbar-item">
                        <StaticImage
                            src='../images/logo.png'
                            alt='Community Improvement Service'
                            width={30}
                            height={30}
                            transformOptions={{fit: "contain"}}
                        />
                    </Link>

                    <button className="navbar-burger" data-target="navMenu" aria-label="menu"
                       aria-expanded="false" onClick={() => {
                        setIsActive(!isActive);
                    }}>
                        <span aria-hidden="true"/>
                        <span aria-hidden="true"/>
                        <span aria-hidden="true"/>
                    </button>
                </div>

                <div className={`navbar-menu has-background-black-bis ${isActive ? "is-active" : ""}`} style={{paddingRight: 20}}>
                    <div className="navbar-end" style={{fontSize: 15}}>
                        <Link to='/' className={`navbar-item has-background-black-bis ${(page === 'home') ? "navTextHighlight" : "navTextNormal"}`}>
                            HOME
                        </Link>
                        <Link to='/our_services' className={`navbar-item has-background-black-bis ${(page === 'our_services') ? "navTextHighlight" : "navTextNormal"}`}>
                            OUR SERVICES
                        </Link>
                        <Link to='/awards' className={`navbar-item has-background-black-bis ${(page === 'awards') ? "navTextHighlight" : "navTextNormal"}`}>
                            AWARDS
                        </Link>
                        <div className="navbar-item has-dropdown is-hoverable">
                            <Link to='/about-us/our-journey' className="navbar-link has-background-black-bis navTextNormal">
                                ABOUT US
                            </Link>

                            <div className="navbar-dropdown has-background-black-bis">
                                <Link to='/about-us/our-journey' className={`navbar-item has-background-black-bis ${(page === 'our_journey') ? "navTextHighlight" : "navTextNormal"}`}>
                                    OUR JOURNEY
                                </Link>
                                <Link to='/about-us/committee-members' className={`navbar-item has-background-black-bis ${(page === 'committee_members') ? "navTextHighlight" : "navTextNormal"}`}>
                                    COMMITTEE MEMBERS
                                </Link>
                                <Link to='/about-us/contact-us' className={`navbar-item has-background-black-bis ${(page === 'contact_us') ? "navTextHighlight" : "navTextNormal"}`}>
                                    CONTACT US
                                </Link>
                            </div>
                        </div>
                        <div className="navbar-item has-dropdown is-hoverable">
                            <Link to='/get-involved' className={`navbar-link has-background-black-bis ${(page === 'get_involved') ? "navTextHighlight" : "navTextNormal"}`}>
                                GET INVOLVED
                            </Link>

                            <div className="navbar-dropdown has-background-black-bis">
                                <Link to='/get-involved/donate' className={`navbar-item has-background-black-bis ${(page === 'donate') ? "navTextHighlight" : "navTextNormal"}`}>
                                    DONATE
                                </Link>
                                <Link to='/get-involved/volunteer' className={`navbar-item has-background-black-bis ${(page === 'volunteer') ? "navTextHighlight" : "navTextNormal"}`}>
                                    VOLUNTEER
                                </Link>
                                <Link to='/get-involved/join-us' className={`navbar-item has-background-black-bis ${(page === 'join_us') ? "navTextHighlight" : "navTextNormal"}`}>
                                    JOIN US
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </nav>
        </header>
    )
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
